  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

@media(max-width: 600px){
  h1{
    font-size: 23px !important;
  }
  h4{
    font-size: 18px !important;
  }
  .Heading{
    margin: 50px auto !important;
  }
}

.give-top-space{
  margin-top: 100px;
}

.Heading{
  width: 100%;
  margin: 100px auto;
}
.Heading h1{
  font-size: 25px !important;
  font-weight: bold;
}

.Heading p{
  font-size: 15px !important;
}

.zero{
  margin: 0 !important;
  padding: 0 !important;
}

.bold{
  font-weight: bold;
}
.App {
  text-align: center;
}

.img-1-container {
  position: relative;
  width: 100%;
  height: 500px;
  margin-top: 50px;
  top: 10px;
}

.img {
  width: 650px;
  height: 500px;
}

.img-1 {
  position: absolute;
  right: 10px;
  top: 0;
  z-index: 0;
}

.Intro-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: absolute;
  top: 300px;
  left: 50px;
  text-align: left;
}

.about-me {
  text-align: left;
}

.about-me ul {
  line-height: 50px;
}

@media (max-width: 887px) and (min-width: 580px) {
  .Intro-container {
    position: relative;
    margin: auto;
    top: 0;
    left: 0;
  }
  .social-media {
    display: none;
  }
  .social-media img {
    display: none;
  }
}

@media (max-width: 580px) {
  .Intro-container {
    position: relative;
    margin: 20px auto;
    top: 0;
    left: 0;
  }
  .img-1-container {
    position: relative;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    height: 300px;
  }
  .img {
    width: 300px;
    height: 300px;
  }
  .Intro-container h1 {
    font-size: 25px !important;
  }
  .social-media {
    display: none;
  }
  .social-media img {
    display: none;
  }
}

.social-media {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.social-media a {
  transition: transform 0.3s ease;
}
.social-media a:hover {
  transform: scale(1.1);
}

.social-media img {
  width: 50px;
  height: 50px;
  margin: 5px;
  border-radius: 10px;
  cursor: pointer;
}

.Progress {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 5px auto;
}

.contact-social-media {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.contact-social-media img {
  width: 50px;
  height: 50px;
  margin: 5px;
  border-radius: 10px;
  cursor: pointer;
}

.eschContact {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

