.projects-container {
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
}

.BlueLine-container{
    width: 100%;
}

.BlueLine{
    width: 70px;
    height: 3px;
    background-color: blue;
    border-radius: 20px ;
    margin: 20px auto;
}

@media(max-width: 600px){
    .BlueLine{
        margin: 10px auto;
    }
}
.skill-container {
  color: inherit;
}
.skill-container:hover {
  text-decoration: none;
  color: inherit;
}
.skill .image {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 80px;
  min-width: 80px;
  width: 100%;
  height: 100%;
}
.skill {
  border: 1px solid lightgrey;
  width: 90%;
  box-shadow: 10px 10px rgb(238, 237, 237);
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.3s ease;
  margin: 0 !important;
}
.skill:hover {
  transform: scale(1.1);
}

.skill > div {
  padding: 0;
  margin: 0;
}

.skill > .content {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
  padding: 10px;
}

.programinglanguage-container {
  color: inherit;
  margin: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.programinglanguage-container:hover {
  text-decoration: none;
  color: inherit;
}
.programinglanguage img {
  min-height: 100px;
  min-width: 100px;
  height: auto;
  width: 275px;
  object-fit: cover;
}
