.programinglanguage {
  border: 1px solid lightgrey;
  width: 100%;
  box-shadow: 5px 10px rgb(238, 237, 237);
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.3s ease;
  background-color: white;
}
.programinglanguage:hover {
  transform: scale(1.1);
}

.programinglanguage > .content {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
  padding: 10px;
}

.project-container {
  color: inherit;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
}

.project-container:hover {
  text-decoration: none;
  color: inherit;
}

.project-container .website-preview {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100px;
  min-width: 100px;
  height: 300px;
  width: 500px;
}

@media (max-width: 500px) {
  .project-container .website-preview {
    width: 300px;
    height: 200px;
  }
  .project-container .project-info-container {
    max-width: 300px;
  }
}

.project-container {
  border: 1px solid lightgrey;
  width: 90%;
  box-shadow: 10px 10px rgb(238, 237, 237);
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.3s ease;
  margin: 0 !important;
}

.project-container:hover {
  transform: scale(1.1);
}

.project-container .project-info-container {
  width: 100%;
  display: grid;
  grid-template-columns: 100px 1fr;
  padding: 10px;
}

.project-container .project-info-container .content {
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 20px;
}

.project-container .project-info-container .image {
  background-size: 90%;
  background-repeat: no-repeat;
  background-position: center;
}

.Navbar{
    display: none;
}

@media(min-width: 663px){
    .Navbar{
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        z-index: 100;
    }
}
.logo {
  width: 40px;
  height: 40px;
}

.Hamburger {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  border-radius: 50%;
  position: fixed;
  top: 5px;
  left: 5px;
  z-index: 10;
}

.Hamburger div {
  width: 20px;
  height: 2px;
  margin: 2px;
  border-radius: 2px;
  background-color: black;
}

.Hamburger:hover,
.Hamburger:active {
  background-color: rgb(214, 214, 214);
}

@media (min-width: 663px) {
  .Hamburger {
    display: none;
  }
}

.Sidebar{
    display: block;
    width: 50vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #f0efed;
    transition: all 0.3s ease-out;
    z-index: 11;
}

.open{
    transform: translateX(0);
}

.close{
    transform: translateX(-100vw);
}

@media(min-width: 664px){
    .Sidebar{
        display: none;
    }
}
.Modal{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0.25;
    background-color: black;
    z-index: 10;
}
.logo-container {
  position: fixed;
  top: 5px;
  right: 10px;
  z-index: 10;
}

@media (min-width: 663px) {
  .logo-container {
    display: none;
  }
}

