.Hamburger {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  border-radius: 50%;
  position: fixed;
  top: 5px;
  left: 5px;
  z-index: 10;
}

.Hamburger div {
  width: 20px;
  height: 2px;
  margin: 2px;
  border-radius: 2px;
  background-color: black;
}

.Hamburger:hover,
.Hamburger:active {
  background-color: rgb(214, 214, 214);
}

@media (min-width: 663px) {
  .Hamburger {
    display: none;
  }
}
